import create from 'zustand'
import { devtools } from 'zustand/middleware'

const store = create(devtools(
  set => ({
    colors: {
      ids: [1, 2, 3, 4, 5, 6, 7],
      items: {
        1: { id: 1, title: 'Синий', hex: '#2d3192', textHex: '#ffffff' },
        2: { id: 2, title: 'Зеленый', hex: '#00a550', textHex: '#ffffff' },
        3: { id: 3, title: 'Красный', hex: '#ec1f24', textHex: '#ffffff' },
        4: { id: 4, title: 'Желтый', hex: '#fef100', textHex: '#000000' },
        5: { id: 5, title: 'Голубой', hex: '#01adee', textHex: '#ffffff' },
        6: { id: 6, title: 'Коричневый', hex: '#a35c4e', textHex: '#ffffff' },
        7: { id: 7, title: 'Белый', hex: '#ffffff', textHex: '#000000' },
      }
    },

    price: {
      sum: 0,
      aviableIds: [1, 2],
      choosenIds: [],
      items: {
        1: { id: 1, title: 'Улица', cost: 1000 },
        2: { id: 2, title: 'Дом', cost: 500 },
      }

    },


    priceUpdate: (id) => set(state => {
      if (state.price.choosenIds.includes(id)) {
        state.price.choosenIds = state.price.choosenIds.filter(i => i !== id)
      } else {
        state.price.choosenIds.push(id)
      }

      let sum = 0
      state.price.choosenIds.map(i => {
        return sum = sum + state.price.items[i].cost
      })
      state.price.sum = sum
    }),


    // adminModeToggle: () => set(state => {
    //   state.adminMode = !state.adminMode
    // }),


    // pointDelete: (id) => set(state => {
    //   state.points.ids = state.points.ids.filter(i => i !== id)
    //   delete state.points.items[id]
    //   state.plans.ids = state.plans.ids.filter(i => i !== id)
    //   delete state.plans.items[id]
    // }),

    // pointCreate: ({ title, day1, day2, day3, day4, day5, day6, day7 }) => set(state => {
    //   let id = 1
    //   if (Math.max(...state.points.ids) > 0) { id = Math.max(...state.points.ids) + 1 }
    //   state.points.ids.push(id)
    //   state.points.items[id] = { id, title }
    //   state.plans.ids.push(id)
    //   state.plans.items[id] = { 1: day1, 2: day2, 3: day3, 4: day4, 5: day5, 6: day6, 7: day7 }
    // }),

  })
))

export default store