import React from 'react'
import classnames from 'classnames'
import './App.css'
import store from '../store'

// import { UserPanel } from './UserPanel'

function App() {
  const { colors, priceUpdate, price } = store()

  React.useEffect(() => { priceUpdate(1) }, [])

  const [streetOn, setStreetOn] = React.useState(true)
  const [buildingOn, setBuildingOn] = React.useState(true)

  const [activeColorId, setActiveColorId] = React.useState(1)
  const [activeColorTitle, setActiveColorTitle] = React.useState(colors.items[1].title)
  const [activeColorHex, setActiveColorHex] = React.useState(colors.items[1].hex)
  const [activeColorTextHex, setActiveColorTextHex] = React.useState(colors.items[1].textHex)

  const [streetText, setStreetText] = React.useState('Лесная')
  const [streetTranslit, setStreetTranslit] = React.useState('Lesnaya street')
  const [streetType, setStreetType] = React.useState('Улица')

  const [buildingNumber, setBuildingNumber] = React.useState('23')
  const [buildingText, setBuildingText] = React.useState('Лесная')


  const colorToggle = (id) => {
    setActiveColorId(id)
    setActiveColorTitle(colors.items[id].title)
    setActiveColorHex(colors.items[id].hex)
    setActiveColorTextHex(colors.items[id].textHex)
  }

  const streetToggle = () => {
    setStreetOn(!streetOn)
  }

  const buildingToggle = () => {
    setBuildingOn(!buildingOn)
  }

  return (
    <div className="app">
      <div className="app__constructor">
        <div className="app-title">Конструктор</div>
        <div className="app-lead">Создайте свою табличку за 1 минуту и отправьте заказ на изготовление. Начните с выбора цвета.</div>
        <p>Выбранный цвет: {activeColorTitle}</p>
        <ul className="colors">
          {colors.ids.map(i => {
            return (
              <li
                key={i}
                className={classnames('colors-item', { 'colors-item--active': i === activeColorId })}
                style={{ background: colors.items[i].hex }}
                onClick={() => colorToggle(i)}>
              </li>)
          })}
        </ul>

        <div className="form-group">
          <div className="checkbox-group">
            <input type="checkbox" id="check-street" onChange={() => { streetToggle(); priceUpdate(1) }} checked={streetOn} />
            <label htmlFor="check-street">Знак с наименованием улицы</label>
          </div>
          <div className="input-group">
            <input type="text" id="input-street__text" disabled={!streetOn} placeholder="Лесная" value={streetText} onChange={e => setStreetText(e.target.value)} />
            <label htmlFor="input-street__text">Наименование</label>
          </div>
          <div className="input-group">
            <input type="text" id="input-street__type" disabled={!streetOn} placeholder="Улица" value={streetType} onChange={e => setStreetType(e.target.value)} />
            <label htmlFor="input-street__type">Тип объекта</label>
          </div>
          <div className="input-group">
            <input type="text" id="input-street__translit" disabled={!streetOn} placeholder="Lesnaya street" value={streetTranslit} onChange={e => setStreetTranslit(e.target.value)} />
            <label htmlFor="input-street__translit">Наименование на англ. языке.</label>
          </div>
        </div>

        <div className="form-group">
          <div className="checkbox-group">
            <input type="checkbox" id="check-building" onChange={() => { buildingToggle(); priceUpdate(2) }} checked={buildingOn} />
            <label htmlFor="check-building">Знак с номером дома</label>
          </div>
          <div className="input-group">
            <input type="text" id="input-building__number" disabled={!buildingOn} placeholder="23" value={buildingNumber} onChange={e => setBuildingNumber(e.target.value)} />
            <label htmlFor="input-building__number">Номер</label>
          </div>

          <div className="input-group">
            <input type="text" id="input-building__text" disabled={!buildingOn} placeholder="Лесная" value={buildingText} onChange={e => setBuildingText(e.target.value)} />
            <label htmlFor="input-building__text">Текст</label>
          </div>
        </div>

        <div className="buy-wrap">
          <button className="btn btn-main" disabled={!streetOn & !buildingOn}>Заказать</button>
          <div className="price">
            <div className="price-text">Стоимость</div>
            <div className="price-summ">{price.sum} ₽</div>
          </div>
        </div>
      </div>
      <div className="app__preview">
        <div className={classnames('sign sign-street', { 'sign__disabled': !streetOn, activeColorId: true })}>
          <svg
            version="1.1"
            id="street"
            xmlns="http://www.w3.org/2000/svg"
            xmlnsXlink="http://www.w3.org/1999/xlink"
            x="0px" y="0px"
            viewBox="0 0 500 200"
            width="500" height="300"
            enableBackground="new 0 0 500 300"
            xmlSpace="preserve">
            <g>
              <path fill={activeColorHex} d="M489,87.3H11c-4.9,0-9,4-9,9v99.3c0,4.9,4,9,9,9h81.7c8.3,0,16.5,2.4,23.6,6.8
              c31.6,19.7,79.8,32.3,133.8,32.3s102.2-12.6,133.8-32.3c7.1-4.4,15.2-6.8,23.6-6.8H489c4.9,0,9-4,9-9V96.3
              C497.9,91.3,493.9,87.3,489,87.3z"/>
              <path fill={activeColorTextHex} d="M487.1,92.7H12.6c-3.7,0-4.4,0.6-4.4,5v95.9c0,3.5,0.8,4.2,4.5,4.2h83.9c8.1,0,16.1,2.2,23,6.1
              c30.8,17.7,60.9,34.1,130.1,34.1c63.7,0,100-16.4,130.8-34.1c6.9-4,14.8-6.1,23-6.1h83.9c3.8,0,4.5-0.5,4.5-4.2l0-95.9
              C491.8,93.3,491.4,92.7,487.1,92.7z"/>
              <path fill={activeColorHex} d="M482.8,97.3H16.9c-3.6,0-4.3,0.5-4.3,4.6V190c0,3.2,0.8,3.9,4.4,3.9h82.4c8,0,15.8,2,22.6,5.6
              c30.3,16.2,56.3,33.8,126.2,33.8c62.6,0,99.7-17.6,130-33.8c6.8-3.6,14.6-5.6,22.6-5.6h82.4c3.7,0,4.4-0.5,4.4-3.8l0-88.1
              C487.4,97.8,487,97.3,482.8,97.3z"/>

              <path id="street1" fill="none" d="M121.9,191.5c30.3,16.2,56.3,33.8,126.2,33.8c62.6,0,99.7-17.6,130-33.8" />
              <text x="0" y="0" textAnchor="middle">
                <textPath xlinkHref="#street1" startOffset="50%">
                  <tspan fill={activeColorTextHex} fontFamily="Arial, sans-serif" fontSize="20px" letterSpacing="3">{streetType.toUpperCase()}</tspan>
                </textPath>
              </text>

              <path id="street2" fill="none" d="M30.3,176.6h438.1" />
              <text x="0" y="0" textAnchor="middle">
                <textPath xlinkHref="#street2" startOffset="50%">
                  <tspan fill={activeColorTextHex} fontFamily="Arial, sans-serif" fontSize="50px" letterSpacing="6">{streetText.toUpperCase()}</tspan>
                </textPath>
              </text>

              <path id="street3" fill="none" d="M30.3,122.6h438.1" />
              <text x="0" y="0" textAnchor="middle">
                <textPath xlinkHref="#street3" startOffset="50%">
                  <tspan fill={activeColorTextHex} fontFamily="Arial, sans-serif" fontSize="18px" fontWeight="400">{streetTranslit}</tspan>
                </textPath>
              </text>
            </g>
          </svg >
        </div >

        <div className={classnames('sign sign-building', { 'sign__disabled': !buildingOn })}>
          <svg
            version="1.1"
            id="building"
            xmlns="http://www.w3.org/2000/svg"
            xmlnsXlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
            viewBox="0 0 500 500"
            width="200" height="200"
            enableBackground="new 0 0 500 500"
            xmlSpace="preserve">
            <g>
              <ellipse fill={activeColorHex} cx="250" cy="250" rx="250" ry="190" />
              <ellipse fill={activeColorTextHex} cx="250" cy="250" rx="227.6" ry="173" />
              <ellipse fill={activeColorHex} cx="250" cy="250" rx="216.5" ry="164.5" />

              <path id="building1" fill="none" d="M35,244.8c0,89.9,96.2,162.9,215,162.9s215-72.9,215-162.9" />
              <text x="0" y="0" textAnchor="middle">
                <textPath xlinkHref="#building1" startOffset="50%" >
                  <tspan fill={activeColorTextHex} fontFamily="Arial, sans-serif" fontSize="48px" letterSpacing="6">{buildingText.toUpperCase()}</tspan>
                </textPath>
              </text>

              <path id="building2" fill="none" d="M114.7,301.5h270.6" />
              <text x="0" y="0" textAnchor="middle">
                <textPath xlinkHref="#building2" startOffset="50%">
                  <tspan fill={activeColorTextHex} fontFamily="Arial, sans-serif" fontSize="200px" letterSpacing="0">{buildingNumber}</tspan>
                </textPath>
              </text>
            </g>
          </svg>
        </div >
      </div >
    </div >
  )
}

export default App